import React, {ChangeEvent, useEffect, useState} from "react";
import {MusicResponseType, MusicTC} from "../../redax/musicReducer";
import {useDispatch, useSelector} from "react-redux";
import {AppStoreType} from "../../redax/store";
import {InputForm} from "./InputForm";
import {MusicResults} from "./MusicResult";


export const Music = () => {

    const [value, setValue] = useState<string>('');
     const music = useSelector<AppStoreType, MusicResponseType>(s => s.music)
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(MusicTC("top 100"))
    }, [])

    const onChange = (e: ChangeEvent<HTMLInputElement>) => {
        setValue(e.currentTarget.value)
    };

    const onClick = () => {
        dispatch(MusicTC(value));
    };

    return (
        <div>
            <div>
                <InputForm onChangeHandler={onChange}
                           onClickHandler={onClick}
                           value={value}
                           placeholderName={"Search Song"}
                           buttonName={"Search"}
                />
            </div>

            {music.results.length !== 0 && <MusicResults state={music.results}
                                                         trackInfo={music.showInfo}
            />}
        </div>
    )
}