import {MusicApi} from "../API/api-music";
import {AppThunkType} from "./store";
import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {GlobalTypeMusic, ResultsMusicType} from "../Types/MusicTypes";

export type MusicResponseType = {
    resultCount: number | null
    results: ResultsMusicType[]
    showInfo: ResultsMusicType | null
    bookmark: number[]
};

const initialState: MusicResponseType = {
    resultCount: null,
    results: [],
    showInfo: null,
    bookmark: [],
};

const slice = createSlice({
    name: "music",
    initialState: initialState,
    reducers: {
        setAllResultSearchAC(state, action: PayloadAction<GlobalTypeMusic>) {
            return {...action.payload, bookmark: [], showInfo: null}
        },
        setBookmarksTrackAC(state, action: PayloadAction<{ trackId: number }>) {
            return { ...state, bookmark: [...state.bookmark, action.payload.trackId]}
        },
        setShowInfoArtistAC(state, action: PayloadAction<{ track: ResultsMusicType | null }>) {
            return {...state, showInfo: action.payload.track}
        },
        deleteBookmarksTrackAC(state, action:  PayloadAction<{ trackId: number }>) {
            return {  ...state, bookmark: state.bookmark.filter(el => el !== action.payload.trackId)}
        },
    }
})

export const musicReducer = slice.reducer
export const {setAllResultSearchAC, setBookmarksTrackAC, setShowInfoArtistAC, deleteBookmarksTrackAC} = slice.actions

export const MusicTC = (title: string): AppThunkType => async dispatch => {
    const response = await MusicApi.getMusicSearchResult(title)
    dispatch(setAllResultSearchAC(response))

}